import React from "react";
import "./EmeraldIronmonMap.css";
import FullHoenn from "./assets/FullHoenn.webp";
import GraniteCaveRoute from "./assets/GraniteCaveRoute.webp";
import NewMauvilleRoute from "./assets/NewMauvilleRoute.webp";
import MeteorFallsRoute from "./assets/MeteorFallsRoute.webp";
import MagmaHideoutRoute from "./assets/MagmaHideoutRoute.webp";
import AquaHideoutRoute from "./assets/AquaHideoutRoute.webp";
import SeafloorCavernRoute from "./assets/SeafloorCavernRoute.webp";
import VictoryRoadRoute from "./assets/VictoryRoadRoute.webp";
import CurrentsRoute from "./assets/CurrentsRoute.webp";
import { MapInteractionCSS } from "react-map-interaction";
import { ControlPanel } from "./components";
import {
  items,
  trainers,
  portalGroups,
  defaultTrainerHeight,
  defaultTrainerWidth,
  defaultItemHeight,
  defaultItemWidth,
  defaultPortalSize,
} from "./data";
import { useAppSelector } from "./IronmonMapUtils/state";
import { BoundingBoxCoords, Item, MapPortal, Trainer } from "./IronmonMapUtils";

// Map coordinates to route/location names for Emerald
function getLocationFromCoordinates(x: number, y: number): string | null {
  // Emerald route mappings - these need calibration based on actual map
  const routeMappings: Array<{name: string, minX: number, maxX: number, minY: number, maxY: number}> = [
    { name: "Route 101", minX: 2000, maxX: 2500, minY: 4000, maxY: 4500 },
    { name: "Route 102", minX: 3000, maxX: 3500, minY: 4000, maxY: 4500 },
    { name: "Route 103", minX: 1000, maxX: 2000, minY: 3500, maxY: 4000 },
    { name: "Route 104", minX: 3000, maxX: 4000, minY: 4500, maxY: 5000 },
    { name: "Route 105", minX: 500, maxX: 1500, minY: 5000, maxY: 5500 },
    { name: "Route 106", minX: 500, maxX: 1000, minY: 5500, maxY: 6000 },
    { name: "Route 107", minX: 1000, maxX: 2000, minY: 5500, maxY: 6000 },
    { name: "Route 108", minX: 2000, maxX: 3000, minY: 5500, maxY: 6000 },
    { name: "Route 109", minX: 3000, maxX: 4000, minY: 5500, maxY: 6000 },
    { name: "Route 110", minX: 4000, maxX: 5000, minY: 4500, maxY: 5000 },
    { name: "Route 111", minX: 5000, maxX: 6000, minY: 4000, maxY: 5000 },
    { name: "Route 112", minX: 5500, maxX: 6500, minY: 3000, maxY: 4000 },
    { name: "Route 113", minX: 6000, maxX: 7000, minY: 2000, maxY: 3000 },
    { name: "Route 114", minX: 7000, maxX: 8000, minY: 1500, maxY: 2500 },
    { name: "Route 115", minX: 8000, maxX: 9000, minY: 2000, maxY: 3000 },
    { name: "Route 116", minX: 8000, maxX: 9000, minY: 1000, maxY: 1500 },
    { name: "Route 117", minX: 7000, maxX: 8000, minY: 500, maxY: 1500 },
    { name: "Route 118", minX: 6000, maxX: 7000, minY: 500, maxY: 1500 },
    { name: "Route 119", minX: 5000, maxX: 6000, minY: 500, maxY: 2000 },
    { name: "Route 120", minX: 4000, maxX: 5000, minY: 500, maxY: 1500 },
    { name: "Route 121", minX: 3000, maxX: 4000, minY: 500, maxY: 1500 },
    { name: "Route 122", minX: 2000, maxX: 3000, minY: 500, maxY: 1500 },
    { name: "Route 123", minX: 1000, maxX: 2000, minY: 500, maxY: 1500 },
    { name: "Route 124", minX: 500, maxX: 1500, minY: 1500, maxY: 2500 },
    { name: "Route 125", minX: 500, maxX: 1500, minY: 2500, maxY: 3500 },
    { name: "Route 126", minX: 1500, maxX: 2500, minY: 2500, maxY: 3500 },
    { name: "Route 127", minX: 2500, maxX: 3500, minY: 2500, maxY: 3500 },
    { name: "Route 128", minX: 3500, maxX: 4500, minY: 2500, maxY: 3500 },
    { name: "Route 129", minX: 4500, maxX: 5500, minY: 2500, maxY: 3500 },
    { name: "Route 130", minX: 5500, maxX: 6500, minY: 2500, maxY: 3500 },
    { name: "Route 131", minX: 6500, maxX: 7500, minY: 2500, maxY: 3500 },
    { name: "Route 132", minX: 7500, maxX: 8500, minY: 2500, maxY: 3500 },
    { name: "Route 133", minX: 8500, maxX: 9500, minY: 2500, maxY: 3500 },
    { name: "Route 134", minX: 9500, maxX: 10500, minY: 2500, maxY: 3500 },
    { name: "Littleroot Town", minX: 2000, maxX: 2500, minY: 4500, maxY: 5000 },
    { name: "Oldale Town", minX: 3000, maxX: 3500, minY: 4500, maxY: 5000 },
    { name: "Petalburg City", minX: 4000, maxX: 5000, minY: 4000, maxY: 4500 },
    { name: "Rustboro City", minX: 6000, maxX: 7000, minY: 1000, maxY: 1500 },
    { name: "Dewford Town", minX: 500, maxX: 1500, minY: 5500, maxY: 6000 },
    { name: "Slateport City", minX: 4000, maxX: 5000, minY: 5000, maxY: 5500 },
    { name: "Mauville City", minX: 5000, maxX: 6000, minY: 3000, maxY: 4000 },
    { name: "Verdanturf Town", minX: 6000, maxX: 7000, minY: 2500, maxY: 3000 },
    { name: "Fallarbor Town", minX: 7000, maxX: 8000, minY: 1000, maxY: 1500 },
    { name: "Lavaridge Town", minX: 5500, maxX: 6500, minY: 3000, maxY: 3500 },
    { name: "Fortree City", minX: 5000, maxX: 6000, minY: 1500, maxY: 2000 },
    { name: "Lilycove City", minX: 3000, maxX: 4000, minY: 500, maxY: 1500 },
    { name: "Mossdeep City", minX: 500, maxX: 1500, minY: 1500, maxY: 2500 },
    { name: "Sootopolis City", minX: 1500, maxX: 2500, minY: 2500, maxY: 3500 },
    { name: "Pacifidlog Town", minX: 8500, maxX: 9500, minY: 2500, maxY: 3500 },
    { name: "Ever Grande City", minX: 1000, maxX: 2000, minY: 500, maxY: 1500 },
  ];
  
  for (const route of routeMappings) {
    if (x >= route.minX && x <= route.maxX && y >= route.minY && y <= route.maxY) {
      return route.name;
    }
  }
  
  return null;
}

export interface MapInteractionCSSValue {
  scale: number;
  translation: BoundingBoxCoords;

  // Translation directions
  // Move map "up": decrease y
  // Move map "down": increase y
  // Move map "right": increase x
  // Move map "left": decrease x
}

export const EmeraldIronmonMap = () => {
  const [mapData, setMapData] = React.useState<MapInteractionCSSValue>({
    scale: 1,
    translation: { x: 0, y: 0 },
  });

  const showRoutes = useAppSelector((state) => state.settings).showRoutes;

  const offsetMapCoords = React.useCallback(
    (x: number, y: number) => {
      setMapData((value: MapInteractionCSSValue) => {
        return {
          ...value,
          translation: {
            x: value.translation.x + x,
            y: value.translation.y + y,
          },
        };
      });
    },
    [setMapData]
  );

  return (
    <div className="ironmon-map">
      <ControlPanel />
      <MapInteractionCSS
        value={mapData}
        onChange={(value: MapInteractionCSSValue) => {
          setMapData(value);
        }}
        maxScale={100}
      >
        <div
          id="portal-label-container"
          className="react-portal-container"
        ></div>
        <div id="tooltip-container" className="react-portal-container"></div>
        {/* TODO: can we get the height and width from the image? Think "FullKanto" is just the string though */}
        {/* if so, then put into variables */}
        <img
          width="12800"
          height="6408"
          src={FullHoenn}
          alt="Full Hoenn"
          className="pixelated full-map-img"
          onClick={(e) => {
            // Get click coordinates relative to image
            const rect = e.currentTarget.getBoundingClientRect();
            const x = (e.clientX - rect.left) * (12800 / rect.width);
            const y = (e.clientY - rect.top) * (6408 / rect.height);
            
            // Map coordinates to route/location names
            const location = getLocationFromCoordinates(x, y);
            if (location) {
              console.log('Map clicked at', x, y, '->', location);
              // Send message to parent window if in iframe
              if (window.parent !== window) {
                window.parent.postMessage({
                  type: 'location',
                  location: location,
                  x: x,
                  y: y
                }, window.location.origin);
              } else {
                // If not in iframe, try to send to opener or use custom event
                window.dispatchEvent(new CustomEvent('mapLocationClick', { detail: { location, x, y } }));
              }
            } else {
              console.log('No location found for coordinates:', x, y);
            }
          }}
        ></img>
        <img
          width="941"
          height="1128"
          style={{
            position: "absolute",
            top: 4508,
            left: 638,
          }}
          alt="Granite Cave Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={GraniteCaveRoute}
        ></img>
        <img
          width="660"
          height="660"
          style={{
            position: "absolute",
            top: 2759,
            left: 2539,
          }}
          alt="New Mauville Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={NewMauvilleRoute}
        ></img>
        <img
          width="1921"
          height="962"
          style={{
            position: "absolute",
            top: 1442,
            left: 637,
          }}
          alt="Meteor Falls Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={MeteorFallsRoute}
        ></img>
        <img
          width="2470"
          height="1030"
          style={{
            position: "absolute",
            top: 2760,
            left: 3842,
          }}
          alt="Magma Hideout Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={MagmaHideoutRoute}
        ></img>
        <img
          width="1788"
          height="547"
          style={{
            position: "absolute",
            top: 755,
            left: 8537,
          }}
          alt="Aqua Hideout Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={AquaHideoutRoute}
        ></img>
        <img
          width="1705"
          height="774"
          style={{
            position: "absolute",
            top: 3682,
            left: 8723,
          }}
          alt="Seafloor Cavern Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={SeafloorCavernRoute}
        ></img>
        <img
          width="736"
          height="1751"
          style={{
            position: "absolute",
            top: 1300,
            left: 12045,
          }}
          alt="Victory Road Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={VictoryRoadRoute}
        ></img>
        <img
          width="3600"
          height="550"
          style={{
            position: "absolute",
            top: 4400,
            left: 4000,
          }}
          alt="Currents Route"
          className={`${showRoutes ? "routes-visible" : "routes-hidden"}`}
          src={CurrentsRoute}
        ></img>
        <svg
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlnsXlink="http://www.w3.org/1999/xlink"
          width="12800"
          height="6325"
          className="svg-container"
        >
          {trainers.map((trainer, index) => {
            return (
              <Trainer
                key={trainer.name.split(" ").join("") + "-" + index}
                height={defaultTrainerHeight}
                width={defaultTrainerWidth}
                {...trainer}
              />
            );
          })}
          {items.map((item, index) => {
            return (
              <Item
                key={"item-" + index}
                height={defaultItemHeight}
                width={defaultItemWidth}
                {...item}
              />
            );
          })}
          {portalGroups.map((portalGroup) => {
            return portalGroup.portals.map((portal, portalIndex) => (
              <MapPortal
                key={"portal-" + portalIndex}
                index={portalIndex + 1}
                scale={mapData.scale}
                offsetMapCoords={offsetMapCoords}
                color={portalGroup.color}
                size={defaultPortalSize}
                {...portal}
              />
            ));
          })}
        </svg>
      </MapInteractionCSS>
    </div>
  );
};
